import React from 'react'
import styled from 'styled-components'
import tw from 'tailwind.macro'
import { Parallax } from 'react-spring/renderprops-addons.cjs'

// Components
import Layout from '../components/Layout'
import ProjectCard from '../components/ProjectCard'

// Elements
import Inner from '../elements/Inner'
import { Title, BigTitle, Subtitle } from '../elements/Titles'

// Views
import Projects from '../views/Projects'
import About from '../views/About'

import fig1 from '../images/01.jpg'

const AboutHero = styled.div`
  ${tw`flex flex-col lg:flex-row items-center mt-8`};
`

const Figure = styled.img`
  ${tw`w-64 xl:w-80 shadow-lg h-auto`};
`

const AboutDesc = styled.p`
  ${tw`text-grey-light text-lg md:text-xl lg:text-2xl font-sans pt-6 md:pt-12 lg:pt-0 lg:pl-12`};
`

const ContactText = styled.p`
  ${tw`text-grey-light font-sans text-xl md:text-2xl lg:text-3xl`};
`

const Footer = styled.footer`
  ${tw`text-center text-grey absolute pin-b p-6 font-sans text-md lg:text-lg`};
`

const Index = () => (
  <>
    <Layout />
    <Parallax pages={5}>
      <About offset={0}>
        <Title>Stellar rotation and eMSTO</Title>
        <AboutDesc>
        Stars spend most of their lifetimes on the “main sequence” (MS) in the Hertzsprung–Russell diagram. Recent discoveries of extended main-sequence turn-offs (eMSTOs) and split main sequences in intermediate-age (around 1-3 Gyr-old) and young clusters (smaller than 400 Myr) pose a fundamental challenge to our traditional understanding of star clusters as prototypes of the canonical single stellar population. We used medium-resolution spectroscopic data to conduct a census of stellar rotation rates of MS stars in various open clusters.
        </AboutDesc>
        <AboutHero>
          <AboutDesc>
        In the Galactic intermediate-age (0.9 Gyr) open cluster NGC5822, we derive the rotational velocities of 24 member stars and find that the loci of the main-sequence stars in the eMSTO region show a clear correlation with the projected rotational velocities in the sense that fast rotators are located on the red side of the eMSTO and slow rotators are found on the blue side. By comparison with a synthetic cluster model, we show that the stellar rotational velocities and the eMSTO of NGC 5822 can be well reproduced, and we conclude that stellar rotation is the main cause of the eMSTO in NGC 5822.
          </AboutDesc>
          <Figure src={fig1} alt="CMD of NGC 5822 with the member stars color-coded by their rotational velocities." />
        </AboutHero>
        <AboutHero>
          <AboutDesc>
        While in another young cluster
          </AboutDesc>
          <Figure src={fig1} alt="Weijia Sun" />
        </AboutHero>
      </About>
    </Parallax>
  </>
)

export default Index
